import * as models from "sakuraba/models";
import i18next = require("i18next");
import { getMegamiDispName } from "./megamiData";
import { t } from "i18next";
import { getCardSetName } from "./cardData";

/**  */
export function logIsVisible(log: state.LogRecord, side: SheetSide): boolean {
    if (log.visibility === 'shown') return true;
    if (log.visibility === 'ownerOnly' && log.side === side) return true;
    if (log.visibility === 'outerOnly' && log.side !== side) return true;
    return false;
}

// 
export function convertLogValueForState(val: LogValue): state.ActionLogBody {
    let params: { [key: string]: state.ActionLogBody } = {};
    if (val[1] !== null) {
        for (let paramName in val[1]) {
            params[paramName] = convertLogParamValueForState(val[1][paramName]);
        }
    }

    return { type: 'ls', key: val[0], params: params };
}

function convertLogParamValueForState(val: LogParamValue): state.ActionLogBody {
    if (typeof val === 'number') {
        return val.toString();
    } else if (typeof val === 'string') {
        return val;
    } else if (Array.isArray(val)) {
        return convertLogValueForState(val);
    } else {
        return val;
    }
}

/**
 *  ()
 */
export function translateLog(log: state.ActionLogBody, detectedLanguage: Language, languageSetting: LanguageSetting): string {
    if (!log) return "";

    let buf = "";
    if (Array.isArray(log)) {
        // 
        return log.map(x => translateLog(x, detectedLanguage, languageSetting)).join('');

    } else {
        // 
        if (typeof log === 'string') {
            return log;
        }
        //  ()
        if (typeof log === 'number') {
            return log.toString();
        }
        // 
        if (log.type === 'ls') {

            let params = {};
            for (let k in log.params) {
                params[k] = translateLog(log.params[k], detectedLanguage, languageSetting); // 
            }

            // i18next
            return t(log.key, params);
        }

        // 
        if (log.type === 'cn') {
            let cardData = new models.CardData(log.cardSet, log.cardId, detectedLanguage, languageSetting, false);
            return cardData.name;
        }

        // 
        if (log.type === 'cs') {
            return getCardSetName(log.cardSet);
        }

        // 
        if (log.type === 'mn') {
            // i18next
            return getMegamiDispName(languageSetting.ui, log.megami);
        }
    }

    return undefined;
}


/**  */
export function getCardRegionTitleLog(
    selfSide: PlayerSide
    , side: PlayerSide
    , region: CardRegion
    , cardSet: CardSet
    , linkedCard: state.Card
): state.ActionLogItem[]
{
    let ret: state.ActionLogItem[] = [];

    // 
    if (selfSide !== side) {
        ret.push({type: 'ls', key: '-'});
    }

    // 
    if (region === 'hand') {
        ret.push({type: 'ls', key: "-"});
    }
    if (region === 'hidden-used') {
        ret.push({ type: 'ls', key: "-"});
    }
    if (region === 'library') {
        ret.push({type: 'ls', key: "-"});
    }
    if (region === 'special') {
        ret.push({type: 'ls', key: "-"});
    }
    if (region === 'used') {
        ret.push({type: 'ls', key: "-"});
    }
    if (region === 'extra') {
        ret.push({type: 'ls', key: "-"});
    }
    if (region === 'on-card') {
        ret.push({type: 'ls', key: '-[CARDNAME]', params: {cardName: {type: 'cn', cardSet: cardSet, cardId: linkedCard.cardId}}});
    }

    return ret;
}

