    if (region === 'life') {
        ret.push({ type: 'ls', key: "-" });
    }
    if (region === 'flair') {
        ret.push({ type: 'ls', key: "-" });
    }
    if (region === 'distance') {
        ret.push({ type: 'ls', key: "-" });
    }
    if (region === 'dust') {
        ret.push({ type: 'ls', key: "-" });
    }
    if (region === 'machine') {
        ret.push({ type: 'ls', key: "-" });
    }
    if (region === 'burned') {
        ret.push({ type: 'ls', key: "-" });
    }
    if (region === 'out-of-game') {
        ret.push({ type: 'ls', key: "-" });
    }
